.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include tablet {
    align-items: flex-end;
  }
  .dark-mode-boton {
    width: 3rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
    filter: invert(100%);
    @include tablet {
      margin-top: 0;
    }
    &:hover{
      cursor: pointer;
    }
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
  @include tablet {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
  @include tablet {
    opacity: 1;
    visibility: visible;
    display: flex;
    align-items: center;
    font-size: 1.8rem;
    gap: 1rem;
  }
  a {
    color: $blanco;
    text-decoration: node;
    font-size: 2.2rem;
    display: block;
    text-align: center;
    @include tablet {
      font-size: 1.8rem;
    }
    &:hover {
      color: $verde;
    }
  }
}
/* MENÚ DE HAMBURGUESA */
.movil-menu {
  @include tablet {
    display: none;
  }
  img {
    width: 5rem;
  }
}
.mostrar {
  height: auto;
  visibility: visible;
  opacity: 1;
  display: flex;
  flex-direction: column;
  @include tablet {
    flex-direction: row;
  }
}
