.contenido-nosotros {
  @include tablet {
    @include grid(2, 2rem);
  }

  picture {
    img {
      width: inherit;
      height: inherit;
    }
  }
}

.texto-nosotros {
  blockquote {
    font-weight: $black;
    font-size: 2.2rem;
    margin: 0rem 0;
    padding: 0rem 0 1rem 0;
  }

}
